import * as Entity from '../entity';
import { Item } from './item';
export class ItemKind extends Entity.EntityKind {
    constructor(config) {
        super(config);
    }
    make(options) {
        const item = new Item(this);
        this.init(item, options);
        return item;
    }
    init(item, options = {}) {
        super.init(item, options);
    }
}
